import React, { useEffect, useState } from 'react';
import './UserPage.scss';
import { useAppDispatch, useAppSelector } from '../../redux/store';
import { setUser2 } from '../../redux/slices/user2';
import { useNavigate } from 'react-router-dom';

const UserPage = () => {
  const user2 = useAppSelector(state => state.user2.user2);
  const dispatch = useAppDispatch();
  const [values, setValues] = useState({
    name: "",
    email: "",
    token: "",
    id: 0,
  })

  const navigate = useNavigate();

  useEffect(() => {
    if (!!user2) {
      if (typeof user2 !== 'string') {
        setValues(user2)
      } else {
        setValues(JSON.parse (user2));
      }
    }
  }, [user2])

  const handleChange = (e) => {
    setValues({ ...values, [e.target.name]: e.target.value })
  }

  const handleLogOut = () => {
    dispatch(setUser2(null));
    localStorage.removeItem('user');
    navigate('/');
  }

  const userData = [
    {
      id: '1',
      name: 'email',
      type: 'email',
      label: 'email',
      errormessage: 'Please enter valid characters only',
      required: true,
      pattern: "[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$",
    },
    {
      id: '2',
      name: 'name',
      type: 'text',
      label: 'name',
      errormessage: 'Please enter valid characters only',
      required: true,
      pattern: "[A-Za-z ]{1,32}",
    },
    // {
    //   id: '3',
    //   name: 'surname',
    //   type: 'text',
    //   label: 'surname',
    //   errormessage: 'Please enter valid characters only',
    //   required: true,
    //   pattern: "[A-Za-z ]{1,32}",
    // },
    // {
    //   id: '4',
    //   name: 'phone',
    //   type: 'phone',
    //   label: 'phone',
    //   errormessage: 'Please enter valid characters only',
    //   required: true,
    //   pattern: "[A-Za-z ]{1,32}",
    // },
    // {
    //   id: '5',
    //   name: 'password',
    //   type: 'password',
    //   label: 'password',
    //   errormessage: 'Please enter valid characters only',
    //   required: true,
    //   pattern: "^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{8,12}$",
    // },
  ]

  useEffect(() => {
    if (localStorage.getItem('user') !== null) {
      dispatch(setUser2(localStorage.getItem('user')))
    }
  }, [])

  return (
    <div className='userPage'>
      <div className="userPage__wrapper">
        <h3 className="userPage__title">My data</h3>
        <form className="userPage__form">
          <div className="userPage__inputWrap">
          
                <label htmlFor="" className="userPage__label">
                  Email
                  <span>*</span>
                </label>
                <div className="userPage__input-field">
                  <input
                    name="email"
                    type="email"
                    pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$"
                    className="userPage__input"
                    onChange={handleChange}
                    value={values.email}
                    required
                  />
                </div>
                <label htmlFor="" className="userPage__label">
                  Name
                  <span>*</span>
                </label>
                <div className="userPage__input-field">
                  <input
                    name="name"
                    type="name"
                    pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$"
                    className="userPage__input"
                    onChange={handleChange}
                    value={values.name}
                    required
                  />
                </div>
                <label htmlFor="" className="userPage__label">
                  Password
                  <span>*</span>
                </label>
                <div className="userPage__input-field">
                  <input
                    name="password"
                    type="password"
                    pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$"
                    className="userPage__input"
                    onChange={handleChange}
                    defaultValue={11111}
                    required
                  />
                </div>
          
          </div>
          <button className="userPage__btn">
            <span>Save changes</span>
          </button>
        </form>
        <button
          className="userPage__logout"
          onClick={() => handleLogOut()}
        >
          <span>Sign out</span>
        </button>
      </div>
    </div>
  )
}

export default UserPage